
.YourBlindDatesGridContainer {
    display: grid;
    max-height:80vh;
    height: 100%;

    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 'BlindLeft YourInnerBlindDatesBody BlindRight';
}
.YourInnerBlindDatesBody {
    grid-area: YourInnerBlindDatesBody;
    border-radius: 20px;
    background: rgba(236, 68, 251, 0.75);
    border: 1px solid #AD00FF;
    text-align: center;
    -ms-flex-line-pack: center;
        align-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -ms-flex-item-align: center;
        align-self: center;
    max-width: 100%;
    max-height: 50vh;
    width: 100%;
    height: 75%;
    overflow-y: auto;
    /*

    */
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}





