
.BlindDatesGridContainer {
	display: grid;
	max-height: 80vh;
	height: 100%;
	overflow-y: hidden;
	grid-template-columns: 1fr 1fr 1fr;
	grid-template-rows: 1fr;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	grid-template-areas: "BlindLeft InnerBlindDatesBody BlindRight";
}
.BlindLeft {
	grid-area: BlindLeft;
	width: 100%;
}
.InnerBlindDatesBody {
	grid-area: InnerBlindDatesBody;
	border-radius: 20px;
	background: rgba(236, 68, 251, 0.75);
	border: 1px solid #ad00ff;
	text-align: center;
	-ms-flex-line-pack: center;
	    align-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-ms-flex-item-align: center;
	    align-self: center;

	max-width: 100%;
	max-height: 50vh;
	width: 100%;
	height: 75%;
	overflow-y: auto;
	/*

	    */
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	/*grid-template-rows: 1fr;
	    grid-template-columns: 1fr;
	    grid-template-areas: 'BodyBorderBox';*/
}
.BlindRight {
	grid-area: BlindRight;
	width: 100%;
}



