
.OptionsPageGridContainer {
	max-height: 80vh;
	height: 80vh;
	display: grid;
	grid-template-columns: 35% 30% 35%;
	grid-template-rows: 10% 30% 60%;
	grid-template-areas:
		"EmptyOptionsLeft OptionsImageButtons EmptyOptionsRight"
		"EmptyOptionsLeft OptionsImage EmptyOptionsRight"
		"EmptyOptionsLeft OptionsTable EmptyOptionsRight";
	text-align: center;
	overflow-y: scroll;
}
.EmptyOptionsLeft {
	grid-area: EmptyOptionsLeft;
	max-width: 100%;
	max-height: 100%;
	width: 100%;
	height: 100%;
}
.EmptyOptionsRight {
	grid-area: EmptyOptionsRight;
	max-width: 100%;
	max-height: 100%;
	width: 100%;
	height: 100%;
}
.OptionsImageButtons {
	grid-area: OptionsImageButtons;
	max-width: 100%;
	max-height: 100%;
	width: 100%;
	height: 100%;
	border-radius: 20px;
}
.OptionsImageButtons > button {
	width: 15%;
	height: 10%;
	border-radius: 20px;
	-ms-flex-item-align: center;
	    align-self: center;
}
.OptionsImage {
	grid-area: OptionsImage;
	border-radius: 20px;
	max-width: 100%;
	max-height: 100%;
	width: 100%;
	height: 100%;
}
.OptionsImage > img {
	border-radius: 20px;
	max-width: 100%;
	max-height: 100%;
	display: block;
	margin-left: auto;
	margin-right: auto;
	width: 50%;
}
.DeleteButton {
	background-color: red;
}
.OptionsTable {
	grid-area: OptionsTable;
	background: rgba(236, 68, 251, 0.75);
	border-radius: 20px;
	margin: 5px;
}



