
.MessagePageContainer {
    height:100%;
    max-height:80vh;
    display: grid;
    grid-template-columns: 10% 45% 45%;
    grid-template-rows: auto;
    grid-template-areas: 'SideBar MyChat YouChat';
}
.SideBar
{
    grid-area: SideBar;
    background-color:gray;
    border-radius:5px;
    margin-top:10px;
    margin-bottom:10px;
    border:2px solid darkGray;
}
.MyChat
{
    grid-area: MyChat;
}
.YouChat
{
    grid-area: YouChat;
}
.right
{
    text-align:right;
}
.left
{
    text-align:left;
}
.LeftGrid
{
    display: grid;
    grid-template-columns: 10% 90%;
    grid-template-rows: auto 60px ;
    grid-template-areas:
        'img Text'
        'img time';
}
.RightGrid
{
    display: grid;
    grid-template-columns: 92% 8%;
    grid-template-rows: auto 60px;
    grid-template-areas:
        'Text img'
        'time img';
}

/* Chat containers */
.container
{
    border: 2px solid #dedede;
    background-color: #f1f1f1;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
}

/* Darker chat container */
.darker
{
    border-color: #ccc;
    background-color: #ddd;
}

/* Style images */
.container img
{
    /*float: left;*/
    max-width: 60px;
    width: 100%;
    margin-right: 0px;
    border-radius: 250px;
}

/* Style the right image */
.container img.right
    {
        /*float: right;*/
        margin-left: 0px;
        margin-right: 0;
}
.Text
{
    grid-area:Text;
}

/* Style time text */
.time
{
    grid-area:time;
    color: #aaa;
}
.img
{
    grid-area:img;
}
.OverflowWindow
{
    overflow-y:scroll;
}



