
#app
{
    margin: 0px;
    padding: 0px;
    font-family: Quantico;
}
button
{
    text-shadow: 1px 0 0 #AD00FF, -1px 0 0 #AD00FF, 0 1px 0 #AD00FF, 0 -1px 0 #AD00FF, 1px 1px #AD00FF, -1px -1px 0 #AD00FF, 1px -1px 0 #AD00FF, -1px 1px 0 #AD00FF;
    background: rgba(236, 68, 251, 0.75);
    border: 1px solid #AD00FF;
    border-radius: 20px;
}
*,* :before, *:after{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin:0;
}
.GridContainer {
    height: 100vh;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: 10vh auto 10vh;
    grid-template-areas: 'Menu' 'Middle' 'Bottom';
    background-image: url('assets/img/NeonTigerLarge.jpg');
    background-size:cover;
}
.Menu
{
    grid-area: Menu;
    background: rgba(236, 68, 251, 0.75);
    border: 1px solid #AD00FF;
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient:horizontal;
    -webkit-box-direction:normal;
        -ms-flex-direction:row;
            flex-direction:row;
    -webkit-box-pack:center;
        -ms-flex-pack:center;
            justify-content:center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.Hotline
{
    /* text-align: center; */
    color: #000000;
    text-shadow: 1px 0 0 #FF0000, -1px 0 0 #FF0000, 0 1px 0 #FF0000, 0 -1px 0 #FF0000, 1px 1px #FF0000, -1px -1px 0 #FF0000, 1px -1px 0 #FF0000, -1px 1px 0 #FF0000;
    /* margin-top:2.5vh; */
    margin-left: 1vw;
    margin-right: 1vw;
    font-size:larger;
}
.MenuButton
{
    margin-left: 2vw;
    margin-right: 2vw;
}
.Middle
{
    grid-area: Middle;
    max-height:100%;
}
.Bottom {
    grid-area: Bottom;
    background: rgba(236, 68, 251, 0.75);
    border: 1px solid #AD00FF;
}



