
.CreateAccountPageContainer
{
    display:grid;
    grid-template-columns:35% 30% 35%;
    grid-template-rows:80vh;
    grid-template-areas:'EmptyCreateLeft CreateAccountTable EmptyCreateRight';
    overflow-y: scroll;
    max-height: 80vh;
}
.EmptyCreateLeft
{
    grid-area:EmptyCreateLeft;
    width:100%;
    height:100%;
}
.CreateAccountTable
{
    grid-area: CreateAccountTable;
    text-align: center;
    -ms-flex-item-align: center;
        align-self: center;
    background: rgba(236, 68, 251, 0.75);
    text-shadow: 1px 0 0 #AD00FF, -1px 0 0 #AD00FF, 0 1px 0 #AD00FF, 0 -1px 0 #AD00FF, 1px 1px #AD00FF, -1px -1px 0 #AD00FF, 1px -1px 0 #AD00FF, -1px 1px 0 #AD00FF;
    overflow-x: hidden;
    max-height:80vh;
}
.EmptyCreateRight {
    grid-area: EmptyCreateRight;
    width: 100%;
    height: 100%;
}



