
.LogInPageGridContainer
{
    display:grid;
    grid-template-columns:33% 33% 33%;
    grid-template-rows:33% 33% 33%;
    grid-template-areas:
        'LoginGridOne LoginGridTwo LoginGridThree'
        'LoginGridFour LoginGridFive LoginGridSix'
        'LoginGridSeven LoginGridEight LoginGridNine';
    text-align:center;
}
.LoginGridOne {
    grid-area: LoginGridOne;
    width: 100%;
    height: 100%;
}
.LoginGridTwo {
    grid-area: LoginGridTwo;
    width: 100%;
    height: 100%;
}
.LoginGridThree {
    grid-area: LoginGridThree;
    width: 100%;
    height: 100%;
}
.LoginGridFour {
    grid-area: LoginGridFour;
    width: 100%;
    height: 100%;
}
.LoginGridFive
{
    grid-area: LoginGridFive;
    width: 100%;
    height: 100%;
    text-align: center;
    border-radius:20px;
}
.LoginGridFive > table
    {
        background: rgba(236, 68, 251, 0.75);
        border-radius:20px;
}
.LoginGridSix {
    grid-area: LoginGridSix;
    width: 100%;
    height: 100%;
}
.LoginGridSeven {
    grid-area: LoginGridSeven;
    width: 100%;
    height: 100%;
}
.LoginGridEight {
    grid-area: LoginGridEight;
    width: 100%;
    height: 100%;
}
.LoginGridNine {
    grid-area: LoginGridNine;
    width: 100%;
    height: 100%;
}
.TableClass {
    width: 50%;
    height: 50%;
    margin-left: 25%;
    margin-right: 25%;
    margin-top: 12.5%;
    text-shadow: 1px 0 0 #AD00FF, -1px 0 0 #AD00FF, 0 1px 0 #AD00FF, 0 -1px 0 #AD00FF, 1px 1px #AD00FF, -1px -1px 0 #AD00FF, 1px -1px 0 #AD00FF, -1px 1px 0 #AD00FF;
}
.TableButton {
    width: 50%;
    border-radius: 20px;
    margin-top: 5px;
}



