
.SwipePageContainer {
		overflow-y: scroll;
    height: 100%;
		display: grid;
		grid-template-columns: 35% 30% 35%;
		grid-template-rows: 10% auto 80% auto 10%;
		grid-template-areas:
    "EmptyLeft SwipeName EmptyRight"
    "EmptyLeft SwipeImageSelector EmptyRight"
    "EmptyLeft SwipeImage EmptyRight"
    "EmptyLeft SwipeBio EmptyRight"
    "EmptyLeft SwipeButtons EmptyRight";
}
.EmptyLeft {
		grid-area: EmptyLeft;
		width: 100%;
		height: 100%;
}
.SwipeName {
		grid-area: SwipeName;
		text-align: center;
		font-size: 20px;
		font-weight: bolder;
		text-shadow: 1px 0 0 #ad00ff, -1px 0 0 #ad00ff, 0 1px 0 #ad00ff,
			0 -1px 0 #ad00ff, 1px 1px #ad00ff, -1px -1px 0 #ad00ff, 1px -1px 0 #ad00ff,
			-1px 1px 0 #ad00ff;
}
.SwipeImageSelector {
		grid-area: SwipeImageSelector;
		text-align: center;
}
.SwipeImageSelector > button {
		border-radius: 20px;
		height: 2.5%;
		width: 10%;
		padding: 0px;
		margin-left: 1px;
		margin-right: 1px;
}
.SwipeImage {
		grid-area: SwipeImage;
		width: 100%;
		height: 100%;
}
.SwipeImage > img {
		max-height: 90%;
		border-radius: 20px;
		display: block;
		margin-left: auto;
		margin-right: auto;
}
.SwipeBio {
		grid-area: SwipeBio;
		text-align: center;
		background: rgba(236, 68, 251, 0.75);
		border-radius: 20px;
}
.SwipeButtons {
		grid-area: SwipeButtons;
		text-align: center;
		border-radius: 20px;
		margin: 5px;
}
.EmptyRight {
		grid-area: EmptyRight;
		width: 100%;
		height: 100%;
}



