
.right
{
    text-align:right;
}
.RightGrid
{
    display: grid;
    grid-template-columns: 92% 8%;
    grid-template-rows: auto 60px;
    grid-template-areas:
        'Text img'
        'time img';
}
.container
{
    border: 2px solid #dedede;
    background-color: #f1f1f1;
    border-radius: 5px;
    padding: 10px;
    margin: 10px;
}
.darker
{
    border-color: #ccc;
    background-color: #ddd;
}
.container img
{
    /*float: left;*/
    max-width: 60px;
    width: 100%;
    margin-right: 0px;
    border-radius: 250px;
}
.container img.right
    {
        margin-left: 0px;
        margin-right: 0;
}
.Text
{
    grid-area:Text;
}
.time
{
    grid-area:time;
    color: #aaa;
}
.img
{
    grid-area:img;
}



